___CSS_LOADER_EXPORT___.push([module.id, `@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0000003a;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  background-color: var(--background-color);
  padding: var(--section-padding);
  border-radius: var(--section-padding);
  margin: 20px;
}

.loading-container h3 {
  font-size: 1.2rem;
}

.loading-container i {
  font-size: 2rem;
  animation: rotate infinite 1s ease;
}
